input[type=range] {
  -webkit-appearance: none;
  width: 100%;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  background: #979797;
}
input[type=range]::-webkit-slider-thumb {
  height: 20px;
  width: 12px;
  background: #d8d8d8;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -10px;
  border-radius: 5px;
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  background: #979797;
}
input[type=range]::-moz-range-thumb {
  height: 20px;
  width: 12px;
  border-radius: 5px;
  background: #d8d8d8;
  cursor: pointer;
  border: 0;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
}
input[type=range]::-ms-fill-lower {
  border: 0.5px solid #979797;
}
input[type=range]::-ms-fill-upper {
  border: 0.5px solid #979797;
}
input[type=range]::-ms-thumb {
  margin-top: 0;
}

input[type=range].event__track__vol-line::-webkit-slider-runnable-track {
  height: 4px;
  cursor: pointer;
  background: #d8d8d8;
}
input[type=range].event__track__vol-line::-webkit-slider-thumb {
  height: 17px;
  width: 17px;
  background: #605757;
  margin-top: -7px;
  border-radius: 50%;
}
input[type=range].event__track__vol-line::-moz-range-track {
  height: 4px;
  background: #d8d8d8;
}
input[type=range].event__track__vol-line::-moz-range-thumb {
  height: 17px;
  width: 17px;
  border-radius: 50%;
  background: #605757;
}
input[type=range].event__track__vol-line::-ms-track {
  height: 2px;
  background: #d8d8d8;
}
input[type=range].event__track__vol-line::-ms-fill-lower {
  border: 0;
}
input[type=range].event__track__vol-line::-ms-fill-upper {
  border: 0;
}
input[type=range].event__track__vol-line::-ms-thumb {
  margin-top: 0;
}
